thead th{
    white-space: nowrap;
}

.table> tbody > tr > td,
.table> thead > tr > th {
    padding: 8px 15px;
}

.table> tbody > tr.active > td{
    background: #E9F8F9;
}

.table-middle > tbody > tr > td {
    vertical-align: middle;
}

// Cancel info
.cancel-info {
	width: 100% ;
	border: 1px solid #eaeaea;
	border-radius:4px;
	border-collapse:separate;

	thead{
		background-color:#f4f4f4;
	}

	thead tr th,
	tbody tr td{
  		padding: 10px 15px;
  		font-size: 1.4rem ;
	}

	tbody td{
		color:#666;
		border-top: 1px solid #eaeaea;
	}

	tbody td span{
		margin-right: 5px;
	}
}
