.page-wrap{
    min-height: calc(100vh - #{$footer-height});  // 讓 footer 不論內容多寡可以保持在最底部
    padding-top: $header-height + 30px;           // header 是 fixed，所以 wrap 需要撐出一個 padding-top
	padding-bottom: 30px;
	background-color: #f4f4f4;

	@media (max-width: $device-sm) {
		min-height: calc(100vh - #{$footer-mobile-height});
		padding-top: $header-mobile-height + 20px;
	}

	.home-banner, .city-banner {
		margin-top: -30px;
	}
}
