.product-detail{
	padding:8px 10px;
	min-height:auto;

	i{
		width:20px;
		color:#ccc;
		text-align: center;
		display:inline-block;
	}

	>h3 i{
		width:auto;
		transition:.3s ease-in-out;
		margin-right:5px;
	}

	.description{
		color:#666;
		font-size: 1.4rem;
		line-height: 1.6;
		margin-bottom:10px;
		max-height:43px;
		overflow:hidden;
		position: relative;
		padding-right:10px;
	}
}

.product {
	&-instant,
	&-place,
	&-time,
	&-notice,
	&-guide-lang{
		font-size: 1.2rem;
		margin-bottom:4px;
		color: $text-light;
	}

	&-instant{
		color:$text-light;;
		font-weight:bold;

		i{
			color: $brand-primary;
		}
	}

	&-footer{
		min-height: 24px;

		>div{
			display: inline-block;
		}

		>.product-star {
			margin-left: 5px;

			&+.divider{
				display: inline-block;
				width:1px;
				background: #eee;
				height: 16px;
				vertical-align: middle;
				margin:0 3px;
			}

			.star{
				width: 10px;
			}
		}
	}

	&-star, &-book{
		font-size: 1.2rem;
		color:#999;
	}

	&-star {
		.star{
			display: inline-block;
			width: 10px;
			height: 10px;
			background-size: contain;
			background-position: center center;
			background-repeat: no-repeat;
			margin-right: 0.5px;
			margin-top:-1px;

			&.star-0{
				background-image: url(../img/ic_star4_0.svg);
			}
			&.star-25{
				background-image: url(../img/ic_star4_1.svg);
			}
			&.star-50{
				background-image: url(../img/ic_star4_2.svg);
			}
			&.star-75{
				background-image: url(../img/ic_star4_3.svg);
			}
			&.star-100{
				background-image: url(../img/ic_star4_4.svg);
			}
		}
	}

	&-pricing{
		font-family: "Arial Rounded MT Bold",'Arial Rounded MT',sans-serif;
		letter-spacing: 0;
		text-align:right;

		span, h4 {
		display: inline-block;
		font-weight: 400;
		margin:0;
		}

		h4{
			color:$brand-primary;
			font-size:2.2rem;
		}

		span{
			color: $text-lighter;
		}
	}

	&-tag{
		padding: 0 8px 2px;
		margin:0px;
		position: absolute;
		bottom: 45px;
		right: -10px;
		text-align: right;
		box-shadow:-1px 1px 1px rgba(0,0,0,0.1);

		&:after{
			content: ' ';
			border-style:solid;
			border-width:0px 10px 4px 10px;
			position:absolute;
			right:-10px;
			bottom:-4px;
		}

		&.bg-primary:after{
			border-color:transparent transparent transparent #04595A;
		}

		span{
			font-size: 1.2rem;
			color: #fff;
			font-weight: bold;
			text-shadow:0 0 2px rgba(0,0,0,.1);
		}
	}

	&-hint{
		position: absolute;
		top: -20px;
		left: -20px;
		padding: 4px 10px;
		font-size: 1.2rem;
		color: #fff;
		box-shadow: 0px 0px 10px rgba(0,0,0,0.3);
		background: $brand-primary;
		border-radius: 4px;
		transition: all ease-in-out .3s;
		opacity: 0;

		i{
			color:#fff;
			margin:0px;
			font-size: 1.4rem;
		}

		&:after{
			display: block;
			content:'';
			width: 0;
			height: 0;
			border-left: 10px solid transparent;
			border-right: 10px solid transparent;
			border-top: 10px solid $brand-primary;
			position: absolute;
			left: 5px;
		}
	}
}

span.origin-price, div.origin-price{
	color:#999;
	font-size:1.2rem;
	font-family:initial;
	text-decoration:line-through;
}
span.origin-price{
	margin-right:8px;
}

.product-item {
	.product-img{
		height:160px;
		position: relative;
		border-radius:5px 5px 0 0;
	}

	.product-detail{
		min-height:auto;

		>h3{
			font-size:1.6rem;
			color:#333;
			font-weight:normal;
			margin:0 0 10px 0;
			line-height: 1.4;
			height: 44px;
			overflow:hidden;
		}

		i{
			width:15px;
		}
	}

	.product-place, .product-book{
		color:#666;
		white-space: nowrap;
		overflow:hidden;
		text-overflow: ellipsis;
		margin-bottom:2px;
		height:20px;
	}

	.product-place{
		padding-right:120px;
	}

	.product-book i,
  	.product-place i{
		margin-left: -2px;
		margin-right:2px;
	}

	.product-footer{
		position: relative;
	}

	.product-pricing{
		right:0;
		bottom:-2px;
		line-height: 1;

		span{
			font-size:1.2rem;
		}

		h4{
			font-size: 1.8rem;
		}
	}

	.product-hint{
		top: 145px;
		left: 0px;
	}
}

.product-item .product-pricing,
.product-listview .product-pricing{
	position: absolute;
}

.product-item:hover .product-hint,
.product-listview:hover .product-hint{
	opacity: 1;
	transform: translateY(-10px);
}

.product-listview {
	background-color: #fff;
	border-radius: 0px;
	box-shadow:none;
	border-radius: 2px;
	margin-bottom: 15px;
	position: relative;
	border-radius:6px;
	transition: 0.25s;

	&:hover{
		opacity: 0.8;
	}

	.img-bg {
		height: 205px;
		margin-right: -10px;
		border-radius:6px 0 0 6px;
	}

	.product-detail{
		padding: 0 20px 10px 0;
		position: relative;
		min-height:166px;

		>h3 {
			font-size: 1.8rem;
			color:#333;
			height:auto;
			max-height:5rem;
			margin:15px 0px 8px 0px;
			line-height: 1.4;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			font-weight:bold;
		}
	}

	.product-pricing{
		right: -30px;
		bottom:2px;

		h4{
			font-size: 2rem;
			line-height:1.2;
		}

		span{
			font-size: 1.4rem;
		}
	}

	.product-footer{
		position: absolute;
		left: 0;
		bottom: -15px;
		width:calc(100% - 50px);
	}

	.product-hint:after {
		left: 12px;
	}
}

.product-item:hover h3 i,
.product-listview:hover h3 i{
	transform:rotateY(360deg);
	color:#FFC612 !important;
	text-shadow:0 0 15px rgba(255,198,18,.2)
}

@media (max-width: $device-sm) {
	.product-listview {
		.img-bg {
			margin: 0;
			border-radius: 6px 6px 0 0;
			height: 160px;
		}

		.product-detail {
			padding: 0 10px 10px;
			min-height: auto;
		}

		.product-footer {
			position: static;

			>div {
				display: block;
				margin-top: 5px;
			}

			.divider {
				display: none;
			}
		}

		.product-pricing {
			right: 10px;
			bottom: 10px;
		}

		.product-tag {
			bottom: 60px;
		}
	}
}
