.chip{
	display: inline-block;
	margin:5px 2px;
	max-width: 100%;
	vertical-align: middle;

	a{
		color: $brand-primary;
		border:1px solid $brand-primary;
		border-radius:100px;
		padding:2px 12px;
		font-size: 1.2rem;
		transition:none;
		white-space: nowrap;
		display:block;
		overflow: hidden;
		text-overflow: ellipsis;

		&hover, &:focus{
			background: $brand-primary;
			color:#fff;
		}
	}
}

.product-result .chip {
	position: relative;

	a {
		color: #666;
		cursor: default;
		padding-right: 25px;
		border-color: #999;
	}

	&.clearall a {
		background: #999;
		color: #fff;
		cursor: pointer;
		padding-right: 12px;
	}

	.delete-chip {
		position: absolute;
		right: 10px;
		top: -1px;
		color: #999;
		cursor: pointer;
		font-style: normal;
		line-height: 24px;
	}
}

