.owl-carousel{
	position: relative;

	.owl-nav [class*=owl-]{
		position: absolute;
		top:50%;
		transform: translateY(-50%);
		color:#999;
		background-color:transparent;
		padding: 0;
		margin: 0;

		&:hover{
			color:#333;
			background-color:transparent;
		}

		&.owl-prev {
			left: -40px;
		}

		&.owl-next {
			right: -40px;
		}

		&.disabled {
			display: none;
		}
	}
}

/*Product/city carousel*/
.home-carousel .owl-item .item{
	margin:auto;
	width:auto;
	border-radius: 6px;
}

@media (max-width: $device-sm) {
	.owl-carousel .product-item{
		width:260px;
	}
}
