.empty {
    text-align: center;
    padding: 30px;

    &-img{
        width:150px;
    }

    &-title {
        margin-top:20px;
        font-weight: bold;
        color:$text-light;
    }
}

// 舊版寫法
.empty-list{
	width:100%;
	border:1px solid #ddd;
	border-radius:6px;
	margin-bottom:30px;
	margin-top:20px;
	padding:30px;
	}
  .empty-list img{
	width:200px;
	}
  .empty-list .none-title{
	text-align: center;
	}
  .empty-list .none-title h3{
	margin-top:15px;
	}
  .empty-list .none-hint{
	color:#666;
	max-width: 80%;
	margin:0 auto;
	}
  .empty-list .section-head{
	margin-top:50px;
	margin-bottom:5px;
	}
  .empty-list .section-head .title{
	font-size: 2rem;
	margin-bottom: 10px;
	}
  .empty-list .none-title img{
	width:140px;
	}
  .empty-list .owl-theme .owl-nav [class*=owl-]{
	background-color:rgba(255,255,255,.9);
	border-radius:100%;
	width:50px;
	height: 50px;
	text-align: center;
	box-shadow:1px 2px 8px rgba(0,0,0,.2);
	}
  .empty-list .owl-theme .owl-nav [class*=owl-]:hover{
	background-color:rgba(255,255,255,1);
	}
  .empty-list .owl-theme .owl-nav [class*=owl-] i{
	width:38px;
	line-height: 48px;
	font-size: 3rem;
	color:#666;
	}
  .empty-list .owl-theme .owl-nav [class*=owl-]:hover i{
	color:#333;
	}
  .empty-list .city-item{
	height:252px;
	}
