.kksearch {
	input {
		background: #fff;
		height: 42px;
	}

	.input-group-btn .btn{
		height: 42px;
		font-size:1.4rem;
		left:1px;

		i{
			top: -5px;
			position: relative;
			color:#fff;
		}
	}

	.clear-input{
		top:0;
		right:50px;
		z-index:100;
	}

	.dropdown-arrow{
		top:0;
		opacity: 1;
		border-color: transparent transparent #fff transparent;
	}
}

.search-dropdown{
	display: none;
	background:#fff;
	border-radius: 6px;
	box-shadow: 0 2px 20px rgba(0,0,0,.15);
	padding:15px 0;
	position: absolute;
	width: 100%;
	top:55px;
	z-index: 999;

	.title{
		color:#333;
		font-size: 1.6rem;
		margin:0 0 15px 20px;
	}

	ul{
		list-style: none;

		li {
			a{
				color:#333;
				display:block;
				padding:5px 10px;
				border-radius:4px;

				&:hover, &:focus{
				background: #f4f4f4;
				text-decoration: none;
				}
			}
		}
	}

	.default-suggest ul{
		margin:0;
		padding:0 10px;
		column-count:3;
		width:100%;

		li{
			padding-right: 10px;

			&:nth-child(-n+10){
				border-right:1px solid #eee;
			}
		}
	}

	.city-suggest{
		display: none;

		ul{
			padding:0 20px;
			margin:0;

			li{
				float: left;
				margin:5px 0;

				a{
					padding:5px 15px;
				}
			}
		}
	}

	.autosuggest{
		display: none;

		ul{
			width:100%;
			padding:0 10px;
			border-bottom:1px solid #eee;
			margin-bottom: 0;

			&:last-child{
				border-bottom:0;
			}

			&.destination{
				border-right: 0;
				padding-bottom:10px;
			}

			&.experience{
				padding-bottom: 0;
				padding-top:10px;

				li{
					border-bottom: 1px solid #eee;
					display: block;

					&:last-child{
						border-bottom:0;
					}

					span{
						display: inline-block;
						overflow:hidden;
						white-space:nowrap;
						text-overflow:ellipsis;

						&.prod_name{
							width:66%;
							padding-top:10px;
						}

						&.city{
							width:33%;
							text-align:right;
							color:#999;
						}
					}
				}
			}
		}
	}

	i{
		color:#ccc;
		margin-right:8px;
	}
}

.hot:after{
	content:'\e104  HOT';
	color:#ef7a70;
	font-weight:bold;
	font-size:1rem;
	display: inline-block;
	font-family: 'Helvetica Neue','Glyphicons Halflings';
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	margin-left:8px;
}

.clear-input {
	position: absolute;
	right:0;
	top:0;
	color:#ccc;
	height:42px;
	padding:0 10px;
	line-height:42px;
	cursor:pointer;
	display: none;

	&:hover{
		color:#666;
	}
}

@media (max-width: $device-sm) {
	.search-dropdown {
		.default-suggest ul{
			column-count:1;

			li{
				border: none !important;
			}
		}

		.autosuggest ul.experience li span{
			&.prod_name{
				width:100%;
			}

			&.city{
				width:100%;
				text-align: left;
			}
		}
	}
}
