.checkbox {
	padding-left: 0;
	margin-bottom: 12px;
	position: relative;
	-webkit-transition: color .25s linear;
	transition: color .25s linear;
	font-size: 14px;
	line-height: 1.5;
	cursor: pointer;
}

.checkbox {
	margin-right: 30px;

	&.checked{
		color: $brand-primary;
	}

	input{
		outline: none !important;
		display: none;
	}

	label {
		padding-left: 0;
	}

}

.filter-box ul.filter-list>li {
	margin: 0;
	padding: 10px 0;
	display: block;
	cursor: pointer;

	label {
		margin: 0;
	}

	i {
		color: #999;
		width: 20px;
	}
}

.filter-box ul.filter-list>li:hover i,
.filter-box ul.filter-list .checked i {
	color: $brand-primary;
}

.filter-box span.checkbox-title {
	color: #666;
}
