ul.time-info{
	margin-left:0;
	list-style:none;

	li{
		padding:15px 0;
		position:relative;

		&:after{
			content:'';
			width:3px;
			height:100%;
			background-color:#eee;
			position:absolute;
			left:-24px;
			top:30px;
		}

		&:last-child:after{
			display:none;
		}
	}

	.time-info-day{
		display:inline-block;
		color:#fff;
		font-weight:bold;
		font-size:1.6rem;
		padding:18px 45px 18px 8px;
		margin-left: -26px;
		margin-bottom:15px;
		background-image: url(../img/bg_time_info_day.svg);
		background-size:100% 100%;
		background-repeat: no-repeat;
		position: relative;

		&:after{
			left:2px;
			top:62px;
			height:50px;
		}
	}
}
.time-info-include:before,
.time-info-spot:before{
	position:absolute;
	text-align:center;
	border-radius:100%;
	z-index:1;
}
.time-info-include{
	font-weight:bold;

	&:before{
		content:'$';
		color:#fff;
		font-weight:bold;
		background-color:#FFC612;
		width:22px;
		height:22px;
		left:-33px;
		top:16px;
	}
}
.time-info-spot:before{
	content:'';
	color:#999;
	background-color:#ccc;
	width:15px;
	height:15px;
	left:-30px;
	top:20px;
}
.time-info-spot .time{
	font-size: 1.6rem;
	margin-top: -2px;
	padding-right: 10px;
	color:#999;
	min-width:60px;
}
.time-info-title{
	display:flex;
	align-items:flex-start;
}
.time-info-img{
	text-align: center;
	margin-top:10px;
	border-radius:4px;
	border:1px solid #f5f5f5;
	overflow: hidden;
}
