header{
    height: $header-height;
    background-color: $brand-primary;
    padding: $header-padding 0;
    position: fixed;
    width:100%;
    top:0;
    left:0;
	z-index: 9999;

    a.logo{
		vertical-align: middle;

        img{
			height: $header-height - $header-padding * 2;
		}
    }

    ul.menu{
        float: right;
        text-align: right;
        padding: 0;
        list-style: none;

        >li{
            float: right;

            >a{
                display: block;
                border-radius: 4px;
                background: transparent;
                color:#fff;
                line-height: 1;
                text-decoration: none;
                padding: 12px 16px;
                margin: 2px 5px;
                &:hover, &:focus{
                    background: rgba(255,255,255,0.1);
                }
                i {
                    margin-left:5px;
                }
                .user-type{
                    background: rgba(255,255,255,0.3);
                    padding: 2px 8px;
                    margin-right: 4px;
					display: inline;
                }
            }

            >.btn{
                border: 1px solid #fff;
                color:#fff;
            }
        }
	}

	.kksearch{
		width: 25%;
		display: inline-block;
		vertical-align: middle;
		margin-left: 15px;
		margin-top: 1px;

		input{
			border: none;
			background: rgba(0,0,0,0.25);
			color: #fff;

			&::-webkit-input-placeholder {
				color: #fff;
				opacity: 0.4;
			}
		}

		.clear-input {
			right: 40px;
			color: #fff;
			opacity: 0.5;
		}

		.input-group-btn .btn {
			background: rgba(0,0,0,0.25);
			border: none;
			padding: 15px 10px;

			i {
				color: #fff;
			}
		}

		.search-dropdown {
			max-width: 650px;
		}

	}
}
.menu-backdrop{
    position: fixed;
}

.burger, .open-search {
	padding: 10px 0 10px 10px;
	margin-top: -3px;
	margin-left: 10px;
    float: right;
	cursor: pointer;
	color: #fff;
}

.burger .bar {
	background-color: #fff;
	border-radius: 1px;
	width: 25px;
	height: 3px;
	margin-bottom: 5px;
}

@media (max-width: $device-md) {
    header{
        height: $header-mobile-height;
        transition: left 0.3s;

        a.logo img{
            height: $header-mobile-height - $header-padding * 2;
        }
    }

    body:not(.login-body) header{
        ul.menu {
            padding: 10px 20px;
            position: fixed;
            top: 0;
            right: -$header-menu-width-sm;
			width: $header-menu-width-sm;
			height: 100%;
			z-index: 1000;
            background-color: #fff;
			text-align: left;
            box-shadow: 0 0 0 100vw rgba(0,0,0,0);
			transition: 0.3s;
			overflow-y: auto;

            &.active{
                right: 0;
                box-shadow: 0 0 0 100vw rgba(0,0,0,0.4);
            }

            .user-type{
                background: #138184;
                display: inline-block;
            }

            > li {
                display: block;
                float: none;
                margin-top:15px;

                > a {
                    color: $text-base;
                    font-size: $font-size-md;
                    padding: 12px 0;
                }

                > .btn{
                    color: $brand-primary;
                    border-color: $brand-primary;
                    margin-top:15px;
                }

                &.dropdown .dropdown-menu{
                    position: relative;
                    padding-left: 30px;
                    float: none !important;
                    box-shadow: none;

                    &:before{
                        display: none;
                    }
				}
			}

			>li.member-dropdown {
				border-bottom: 1px solid #eee;
				margin: 0 -20px;
				padding: 0 20px;

				.dropdown-menu{
					display: block;
					padding-left: 0;

					>li>a {
						padding: 12px 16px;
						margin: 2px -10px;
						font-size: 1.6rem;
					}
				}
			}
        }
    }
    body.menu-open{
        left: -$header-menu-width-sm;
        cursor: pointer;

        header{
            left: -$header-menu-width-sm;
        }

        .menu-backdrop{
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
	}

	header .kksearch{
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		right: -100%;
		background: $brand-primary;
		margin: 0;
		z-index: 1001;
		overflow: hidden;
		transition: .3s ease-out;
		opacity: 0;
	}
	header .kksearch.slideIn{
		right: 0;
		opacity: 1;
	}
	header .kksearch .input-group{
		padding: 0 15px;
    	margin-top: 20px;
	}
	header .kksearch .search-dropdown{
		display: block !important;
		position:absolute;
		top:110px;
		bottom:0;
		box-shadow: none;
		border-radius: 0;
		max-width: none;
		overflow-y:auto;
		-webkit-overflow-scrolling:touch;
	}
	header .kksearch-header {
		color: #fff;
	}
	header .kksearch-header h4{
		margin:10px 0 0;
	}
	header .close-search{
		position: absolute;
		top:6px;
		left:15px;
	}
}

@media (max-width: $device-sm) {
	body:not(.login-body) header{
		ul.menu {
			right: -$header-menu-width-xs;
			width: $header-menu-width-xs;
		}
	}

	body.menu-open{
        left: -$header-menu-width-xs;
        cursor: pointer;

        header{
            left: -$header-menu-width-xs;
		}
	}
}
