.loading-card {
	list-style: none;
	padding: 0;
}

.fake {
	animation-duration: 1s;
	animation-fill-mode: forwards;
	animation-iteration-count: infinite;
	animation-name: placeHolderShimmer;
	animation-timing-function: linear;
	background: #eaeaea;
	background: linear-gradient(295deg, #eaeaea 8%, #e0e0e0 28%, #eaeaea 50%);
	background-size: 1200px 300px;
	position: relative;

	.img-bg {
		background: transparent;
	}

	.product-detail {
		height: 205px;
		padding: 0;
		position: relative;
	}

	.mask {
		position: absolute;
		background: #fff;
		width: 100%;
	}

	.list-top-mask {
		height: 15px;
		top: 0;
		left: -5px;
		border-radius: 0 6px 0 0;
	}

	.list-bottom-mask {
		height: 150px;
		bottom: 0;
		left: -5px;
		border-radius: 0 0 6px 0;
	}

	.list-left-mask {
		width: 15px;
		height: 100%;
		bottom: 0;
		left: -20px;
	}

	.list-right-mask {
		width: 15px;
		height: 100%;
		bottom: 0;
		right: -1px;
		border-radius: 0 6px 6px 0;
	}

	.list-middle-mask {
		height: 10px;
		top: 30px;
		left: -15px;
	}

	.list-right-mask-1 {
		width: 20%;
		height: 25px;
		top: 15px;
		right: 0px;
	}

	.list-right-mask-2 {
		width: 60%;
		height: 20px;
		top: 35px;
		right: 0px;
	}
}
