.breadcrumb {
    background-color: transparent;
    padding: 0;
    margin-bottom: 10px;
	font-size: 1.3rem;

	li + li:before {
		color: #26BEC9;
		content: "\f105 ";
		font: normal normal normal 14px/1 FontAwesome;
		text-rendering: auto;
		margin-right: 5px;
		color:#999;
		padding:0;
	}

	a {
		&:hover {
			text-decoration: $link-hover-decoration;
		}

		i {
			margin-right: 5px;
		}
	}
}
