.form-group {
    margin-bottom: 20px;
}

label {
    display: inline-block;
    font-weight: normal;
    color: $text-light;
    user-select: none;
}

.form-control, .form-control[readonly] {
    padding: 10px 12px;
    font-size: $font-size-base;
    line-height: $line-height-base;
    color: $input-color;
    background-color: $input-bg;
    height: 42px;
    border: 1px solid $input-border;
    box-shadow: none;
    appearance: none;

    // Customize the `:focus` state to imitate native WebKit styles.
    @include form-control-focus;

    // Placeholder
    @include placeholder;

    &[disabled],
    fieldset[disabled] & {
        background-color: $input-bg-disabled;
        opacity: 1;
    }
}

// Feedback states
.has-error {
    @include form-control-validation($state-danger-text, $state-danger-border, $state-danger-bg);

    .tip{
        color: $state-danger-border;
    }
  }


select.form-control{
    background-image: url(../img/bg_dropdown.svg);
    background-position: bottom 15px right 12px;
    background-repeat: no-repeat;
    padding-right: 30px;
}

.file-upload-area input[type=file]{
    opacity: 0;
    position: absolute;
    top:0;
    left:0;
    width:100%;
    cursor: pointer;
}

// File upload
.file-upload-area {
    min-height: 180px;
    margin-top: 10px;
    padding: 30px;
    border: 2px dashed #eee;
    background-color: #fafafa;
    border-radius: 4px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    label {
        display: block;
        margin-bottom: 15px;
        font-size: $font-size-xl;
        color: $text-base;

        @media (max-width: $device-sm) {
            font-size: $font-size-lg;
        }
    }

    img {
        width: auto;
        max-width: 100%;
        margin: 10px 0;
    }

    .file-upload{
        position: relative;
        display: inline-block;
    }

    .delete-file{
        margin-left: 5px;
        font-size: 1.8rem;
        text-decoration: none;
    }
}

.form-footer{
    text-align: right;

    @media (max-width: $device-sm) {
        text-align: center;
    }

    label {
        margin-right: 20px;

        @media (max-width: $device-sm) {
            display: block;
            margin: 20px;
        }
    }
}

.input-group-addon{
    background:transparent;
    color: $text-lighter;
    border:1px solid $input-border;
}

.form-btns {
    text-align: right;

    .btn {
        margin-left: 5px;
    }

    .btn-default {
        border-color: transparent;
    }
}

.form-inline {
	margin-top: 10px;

	label {
		display: inline-block;
		margin-right: 20px;
		color: $text-base;
	}
}

.fill-group{
	color: $text-base;
	font-size: 1.5rem;
	margin-bottom: 15px;

	label{
		display: block;
		font-size: 1.3rem;
		color: $text-lighter;
	}

	.tip{
		color: $text-light;
	}

	ol{
		padding-left: 20px;
	}
}

.input-group-clear {
    position: relative;
}
