ul.map-info{
	margin-left: 0;
	padding-left: 0;
	padding-bottom:20px;
	font-size: 1.4rem;
	list-style: none;

	>li{
		margin-bottom:20px;

		> .map-title{
			padding-left: 14px;
			margin-bottom: 5px;

			i{
				color:#ccc;
				margin-right: 5px;
				margin-left: -14px;
			}
		}
	}

	&:last-child,
  	> li:last-child{
		margin-bottom:0;
		padding-bottom:0;
	}

	ul li span{
		margin-bottom:10px;
	}

	.map-img{
		border-radius:4px;
		overflow: hidden;

		img{
			width:100%;
			height:auto;
		}
	}

	#map_target,
	#map_source{
		padding-bottom:56.26%;
	}
}
