.dropdown-menu{
    top: 100%;
	min-width: 200px;
    padding: 8px;
    margin: 2px 0 0;
    border: none;
    border-radius: 6px;
    box-shadow: 0 6px 12px rgba(0,0,0,.1);

    &:before{
        content: '';
        border-width: 6px;
        border-style: solid;
        border-color: transparent transparent #fff transparent;
        position: absolute;
        top: -12px;
        left: 20px;
    }

    &.pull-right:before{
        left: auto;
        right: 20px;
    }

    .divider {
        margin: 8px -8px;
    }

    > li > a {
        padding: 6px 12px;
        clear: both;
        font-weight: normal;
        color: $text-base;
        border-radius: 4px;
		white-space: nowrap; // prevent links from randomly breaking onto new lines

		&:hover, &:focus, &:active {
			background-color:rgba(0,0,0,0.04);
		}

        @media (max-width: $device-sm) {
            font-size: 1.6rem;
            padding:8px 16px;
        }
	}

	&.dropdown-menu-right:before {
		left:auto;
		right:15px;
	}
}

.dropdown-arrow{ /* Top arrow ▲ of Dropdown Menu  */
	border-style: solid;
	border-width: 0 8px 7px;
	border-color: transparent transparent #fff transparent;
	margin-top: -5px;
	width: 0;
	height: 0;
	opacity: 0;
	position: absolute;
	left: 19px;
	top: 100%;
	z-index: 1001;
}

.open>.dropdown-arrow{
	opacity:1;
}

// Active state
.dropdown-menu > .active > a {
    &,
    &:hover,
    &:focus {
        color: $brand-primary;
        background-color: transparent;
    }
}

.sorting{
	text-align:right;
	margin-bottom:10px;
	margin-top:10px;

	.dropdown{
		display: inline-block;

		>span {
			color: $text-lighter;
			vertical-align:middle;
		}

		.btn, .btn:focus {
			background-color: transparent;
			border: 0;
			padding: 5px;
		}
	}
}

.dropdown-scroll .dropdown-menu {
	max-height: 300px;
	overflow-y: auto;
	overscroll-behavior-y: contain;
}
