.order-main-goday{
	color: $text-light;
	font-family: "Arial Rounded MT Bold",'Arial Rounded MT',sans-serif;
	text-align: center;
	background: $bg-grey-lighter;
	box-shadow: 0px 2px 0px rgba(0,0,0,0.1);
	width:70px;
	border-radius: 4px;
	margin:10px 15px 15px 0;
	padding:0 0 4px 0;

	.year{
		background: $brand-primary;
		border-radius:4px 4px 0 0;
		color:#fff;
		font-size: 1.2rem;
		line-height:1;
		letter-spacing: 1px;
		padding:4px;
		position: relative;
	}

	.mon{
		font-size:1.6rem;
		line-height:1.2;
		margin-top:5px;
		text-transform:uppercase;
	}

	.day{
		font-size:2.6rem;
		line-height:1;
		margin-bottom:2px;
	}
}
