html, body{
    -webkit-overflow-scrolling: touch;
}
html{
    font-size: 10px;
}
body{
    font-family: 'Helvetica Neue','Helvetica','Arial',sans-serif;
    line-height: $line-height-base;
    letter-spacing: 0.5px;
    position: relative;
    left: 0;
    transition: left 0.3s;
}
[lang^="zh-tw"] body{
    font-family:'Noto Sans TC',sans-serif;
}
[lang^="zh-cn"] body{
    font-family:'Noto Sans SC',sans-serif;
}

* {
    outline: none;
}
a {
    color: $link-color;
    text-decoration: none;

    &:hover,
    &:focus {
        color: $link-hover-color;
        text-decoration: none;
        outline: none;
    }

}

hr {
    margin: 40px 0;

    @media (max-width: $device-sm) {
        margin: 25px 0;
    }
}

// Width
.w-80 {
    width: 80px;
}
.w-100 {
    width: 100px;
}
.w-150 {
    width: 150px;
}

// Margin
.mt-10 {
    margin-top: 10px !important;
}
.mt-20 {
    margin-top: 20px !important;
}
.mt-40 {
    margin-top: 40px !important;
}

// Icons
.icons{
    vertical-align: middle;
    display: inline-block;
    margin-top: -3px;
}

// Background color
.bg-primary {
	background-color: $brand-primary;
}

// Text color
.text-primary {
    color: $brand-primary !important;
}
.text-info {
    color: $brand-info !important;
}
.text-danger {
    color: $brand-danger !important;
}
.text-light {
    color: $text-light;
}
.text-lighter {
    color: $text-lighter;
}

// Text Size
h1, h2, h3, h4{
    font-family:inherit;
    font-weight: 700;
}
h5, h6{
    font-family:inherit;
    font-weight: 400;
}
h1{
    font-size:2.4rem;
    line-height: 1.5;
}
h2{
    font-size:2.2rem ;
    line-height: 1.7;
}
h3{
    font-size:2rem ;
    line-height: 1.7;
}
h4{
    font-size:1.6rem ;
    line-height: 1.7;
}
h5{
    font-size:1.4rem ;
    line-height: 1.7;
}

// Title
.title{
    font-size: 3rem;
    font-weight: bold;
    margin:0;
    margin-bottom: 30px;
}
.section-title{
    font-size: $font-size-xl;
    margin-top:0;
    margin-bottom:30px;
}
@media (max-width: $device-sm) {
    .title{
        font-size: $font-size-xxl;
    }
    .section-title{
        font-size: $font-size-lg;
    }
}
.critical-info{
    margin-bottom: 8px;
    padding-left: 23.5px;

    > i {
        margin-right: 5px;
        margin-left: -23.5px;
    }
}

// Text setting
.text-center {
    text-align: center !important;
}
.no-wrap {
    white-space: nowrap;
}
.break-all {
    word-break: break-all;
}

// others
.img-bg {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	overflow: hidden;
}
.info-section{
	padding:30px 0;

	img{
		width:100%;
	}
}
.info-title{
	font-size:2rem;
	color: #333;
	line-height: 1;
	margin-top:0;
	margin-bottom:40px;
	position: relative;

	&:before{
		content:'';
		display: inline-block;
		height:1px;
		width:40px;
		background-color:#333;
		position: absolute;
		left: 0;
		bottom:-8px;
	}
}
.info-subtitle{
	font-size: 1.6rem;
	margin: 25px 0 10px 0;
}
.info-content{
	padding-left:20px;

	ul, ol {
		padding-left:20px;
	}

	ul li, ol li{
		padding:5px 0;
		word-break: break-word;
	}
}

.row-dense {
	margin-right: -7.5px;
	  margin-left: -7.5px;

	[class*=col-]{
		padding-right: 7.5px;
		padding-left: 7.5px;
	}
}
